import React, { Component } from 'react';
import Info from './components/Info.jsx';
import Form from './components/Form.jsx';
import Weather from './components/Weather.jsx';

const API_KEY = 'b6907d289e10d714a6e88b30761fae22';


export default class App extends Component {
  state = {
    temp: undefined,
    city: undefined,
    country: undefined,
    wind: undefined,
    sky: undefined
  }
  
  gettingWeather = async (e) => {
    e.preventDeafault();
    let city =e.target.elements.city.value;

    const api_url = await
    fetch(`https://samples.openweathermap.org/data/2.5/forecast?q=${city}&appid=${API_KEY}`);
    const data = await api_url.json();
    let wind = data.wind;
    let dt_txt = data.dt_txt
    let date =new Date();
    date.setTime(dt_txt);

    this.setState({
      temp: data.main.temp,
      city: data.name,
      country: undefined,
      wind: undefined,
      sky: undefined
    })
  }

  render() {
    return (
      <div>
        <Info />
        <Form weatherMethod ={this.gettingWeather} />
        <Weather />        
      </div>
    )
  }
}
